import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {

    getSalidasMedicamentos(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-salud/farmacia/venta', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

    fetchReceta(ctx, params) {
      return new Promise((resolve, reject) => {
        axios
          .get('/servicio-salud/farmacia/buscar-receta', { params })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },

  },
}
