<template>
  <div>

    <!-- <b-input-group class="border">
      <b-input-group-prepend
        is-text
      >
        <feather-icon icon="SearchIcon" />
      </b-input-group-prepend> -->
    <span>
      <vue-autosuggest
        ref="autocomplete"
        v-model="query"
        :suggestions="suggestions"
        :input-props="inputProps"
        :section-configs="sectionConfigs"
        :render-suggestion="renderSuggestion"
        :get-suggestion-value="getSuggestionValue"
        @input="fetchResults"
      />
    </span>

    <!-- </b-input-group> -->

  </div>

</template>

<script>
import {
// BInputGroup, BInputGroupPrepend,
} from 'bootstrap-vue'
import { VueAutosuggest } from 'vue-autosuggest'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
  components: {
    VueAutosuggest,
    // BInputGroup,
    // BInputGroupPrepend,

  },
  data() {
    return {
      query: '',
      results: [],
      timeout: null,
      selected: null,
      debounceMilliseconds: 500,
      photosUrl: '/buscar-persona',
      inputProps: {
        id: 'autosuggest__input_ajax',
        placeholder: 'Buscar por apellidos, nombres o DNI ',
        class: 'form-control',
        name: 'ajax',
      },
      suggestions: [],
      sectionConfigs: {
        personas: {
          limit: 30,
          label: 'Alumnos',
          onSelected: selected => {
            this.selected = selected.item
            this.$emit('cargarPersonaSelect', selected.item)
          },
        },
        docentes: {
          limit: 30,
          label: 'Docentes',
          onSelected: selected => {
            this.selected = selected.item
            this.$emit('cargarPersonaSelect', selected.item)
          },
        },
      },
    }
  },
  methods: {
    fetchResults() {
      const { query } = this

      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        const personasPromise = this.$http.get(`${this.photosUrl}/${query}`)

        Promise.all([personasPromise]).then(values => {
          this.suggestions = []
          this.selected = null

          const personas = values[0].data.data[0]
          const docentes = values[0].data.data[1]
          // eslint-disable-next-line no-unused-expressions
          personas.length
            && this.suggestions.push({ name: 'personas', data: personas })
          // eslint-disable-next-line no-unused-expressions
          docentes.length
            && this.suggestions.push({ name: 'docentes', data: docentes })
        }).catch(error => {
          this.$toast({
            component: ToastificationContent,
            props: {
              title: `${error.response.data.error}`,
              icon: 'EditIcon',
              variant: 'danger',
            },
          })
        })
      }, this.debounceMilliseconds)
    },
    renderSuggestion(suggestion) {
      return (
        <div>
          <span>{suggestion.item.apellido_paterno} {suggestion.item.apellido_materno}, {suggestion.item.nombres}</span>
          <span class="text-warning font-weight-bold ml-1">{suggestion.item.numero_documento}</span>
        </div>
      )
    },
    getSuggestionValue(suggestion) {
      const { item } = suggestion
      let documentoIdentidad = item.numero_documento
      if (suggestion.item.numero_documento === undefined) {
        documentoIdentidad = ''
      }
      return `${item.apellido_paterno} ${item.apellido_materno}, ${item.nombres} ${documentoIdentidad} `
    },
  },
}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-autosuggest.scss';
</style>
